import React, { useContext, useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import {
  Button, Form, Input,
} from 'antd';
import { Context } from '../../containers/Mtproto';

function Login() {
  const navigate = useNavigate();
  const {
    login,
    loginDel,
    loginApi,
    codeCallback,
    codeCallbackDel,
    codeCallbackApi,
    fetching,
  } = useContext(Context);

  const [codeDisplay, setCodeDisplay] = useState(false);

  const onFinish = (values) => {
    if (codeDisplay) {
      codeCallbackApi(values.code);
    } else {
      loginApi(values.phone, values.password);
    }
  };

  useEffect(() => {
    if (login?.status) {
      setCodeDisplay(true);
    }
  }, [login]);

  useEffect(() => {
    if (codeCallback?.status) {
      loginDel();
      codeCallbackDel();
      navigate('/mtproto/users');
    }
  }, [codeCallback]);

  return (
    <Form
      name="normal_login"
      className="login-form"
      initialValues={{ remember: true }}
      onFinish={onFinish}
    >
      <Form.Item
        name="phone"
        rules={[{ required: true, message: 'Please input your Phone!' }]}
      >
        <Input
          prefix={<UserOutlined className="site-form-item-icon" />}
          placeholder="+123456789"
        />
      </Form.Item>
      <Form.Item
        name="password"
        rules={[{ required: false, message: 'Please input your Password!' }]}
      >
        <Input.Password
          prefix={<LockOutlined className="site-form-item-icon" />}
          placeholder="Password"
        />
      </Form.Item>
      {codeDisplay && (
        <Form.Item
          name="code"
          rules={[{ required: true, message: 'Please input your Code!' }]}
        >
          <Input
            prefix={<LockOutlined className="site-form-item-icon" />}
            type="code"
            placeholder="123456"
          />
        </Form.Item>
      )}
      <Form.Item>
        <Button type="primary" htmlType="submit" className="login-form-button" loading={fetching}>
          {codeDisplay ? 'Login TG' : 'Request Code'}
        </Button>
      </Form.Item>
    </Form>
  );
}

export default Login;
