import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import {
  Button,
  Divider, Table,
} from 'antd';
import { Context } from '../../containers/Mtproto';

function Users() {
  const navigate = useNavigate();
  const {
    userDetails,
    userDetailsApi,
    fetching,
  } = useContext(Context);

  const [rows, setRows] = useState();
  const [columns, setColumns] = useState();

  useEffect(() => {
    userDetailsApi();
  }, []);

  useEffect(() => {
    if (userDetails) {
      setRows(userDetails.items.map(({ key, ...rest }) => ({ key, ...rest })));
    }
  }, [userDetails]);

  useEffect(() => {
    if (rows && rows.length > 0) {
      setColumns(Object.keys(rows[0]).map((key) => ({
        title: key,
        dataIndex: key,
        sorter: (a, b) => a[key] > b[key],
      })));
    }
  }, [rows]);

  return (
    <>
      <Button onClick={() => navigate('/mtproto/login')}>add new TG account</Button>
      <Divider />
      <Table
        columns={columns}
        dataSource={rows}
        loading={fetching}
      />
    </>
  );
}

export default Users;
