import React from 'react';
import { useParams } from 'react-router-dom';
import Login from './Login';
import Users from './Users';
import Frame from '../../components/Frame';
import MtprotoContainer from '../../containers/Mtproto';

function MtprotoPage() {
  const { action } = useParams();
  const routes = {
    default: <Users />,
    login: <Login />,
    users: <Users />,
  };

  return (
    <Frame>
      <MtprotoContainer>
        {routes[action] || routes.default}
      </MtprotoContainer>
    </Frame>
  );
}

export default MtprotoPage;
