import React, {
  useState, useCallback, useMemo,
} from 'react';
import { Modal } from 'antd';
import PropTypes from 'prop-types';
import cloud from '../api/mtproto';

export const Context = React.createContext();

function MtprotoContainer({ children }) {
  const [data, setData] = useState({});
  const [fetching, setIsFetching] = useState(false);
  const fetchData = useCallback(async (apiName, fetcher, ...params) => {
    setIsFetching(true);
    try {
      const response = await fetcher(...params);
      setData({
        ...data,
        ...{ [apiName]: response.data },
      });
      return response.data;
    } catch (e) {
      Modal.error({
        title: 'error',
        content: `${e}`,
      });
    } finally {
      setIsFetching(false);
    }
    return null;
  });
  const fetcher = Object.keys(cloud).map(
    (key) => ({
      [`${key}Api`]: (...params) => fetchData(key, cloud[key], ...params),
      [`${key}Del`]: () => {
        delete data[key];
        setData({ ...data });
      },
    }),
  ).reduce((a, b) => ({
    ...a,
    ...b,
  }));

  return (
    <Context.Provider
      value={useMemo(() => ({
        ...data,
        ...fetcher,
        fetching,
      }), [data, fetcher, fetching])}
    >
      {children}
    </Context.Provider>
  );
}

MtprotoContainer.propTypes = {
  children: PropTypes.node,
};

MtprotoContainer.defaultProps = {
  children: <div />,
};

export default MtprotoContainer;
