import React from 'react';
import { useNavigate } from 'react-router-dom';
import { Typography, Card, Button } from 'antd';
import Frame from '../../components/Frame';
import UserModule from '../../modules/User';

function Home() {
  const navigate = useNavigate();
  return (
    <Frame>
      <Card title="SCP">
        {
          UserModule.isAuthenticated() ? <Typography.Title level={1}>{`welcome ${UserModule.getUsername()}`}</Typography.Title>
            : <Button style={{ 'margin-bottom': '27px' }} onClick={() => navigate('/user/login')}>Login</Button>
        }
        <Card.Meta title="Manage Backend" description="Copyright © SCP" />
      </Card>
    </Frame>
  );
}

export default Home;
