import React, { useMemo } from 'react';
import {
  BrowserRouter, Routes, Route,
} from 'react-router-dom';
import { ConfigProvider, theme } from 'antd';
import Router from './constants/router';
import MainMenu from './pages/MainMenu';
import useTheme from './hooks/localStorage';
import UserModule from './modules/User';
import './App.css';

function App() {
  const links = useMemo(() => Router.generateLinks(), []);
  const { localTheme, setLocalTheme } = useTheme();

  return (
    <div className="App">
      <ConfigProvider
        theme={{
          algorithm: localTheme ? theme.darkAlgorithm : theme.defaultAlgorithm,
        }}
      >
        <BrowserRouter>
          <MainMenu localTheme={localTheme} setLocalTheme={setLocalTheme} />
          <Routes>
            {
              links.map(({
                key,
                component,
                path,
                exact,
              }) => {
                const userIsLogin = UserModule.isAuthenticated();
                if (!userIsLogin) { /* empty */ }
                return (
                  <Route key={key} element={component} path={path} exact={exact} />
                );
              })
            }
          </Routes>
        </BrowserRouter>
      </ConfigProvider>
    </div>
  );
}

export default App;
