import React from 'react';
import Home from '../pages/home';
import User from '../pages/user';
import Mtproto from '../pages/mtproto';

const generateLinks = () => [
  {
    key: 'Home',
    component: <Home />,
    path: '/',
    exact: true,
  },
  {
    key: 'User',
    component: <User />,
    path: '/user/:action?',
  },
  {
    key: 'Mtproto',
    component: <Mtproto />,
    path: '/mtproto/:action?',
  },
];

export default {
  generateLinks,
};
