import React, { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { UserOutlined } from '@ant-design/icons';
import {
  Space, Divider, Button, Input, Avatar, Typography, message,
} from 'antd';
import { Context } from '../../containers/User';
import UserModule from '../../modules/User';

function Account() {
  const navigate = useNavigate();
  const [password, setPassword] = useState('');
  const {
    changePassword,
    changePasswordApi,
    fetching,
  } = useContext(Context);

  useEffect(
    () => {
      if (changePassword) {
        if (changePassword.status) {
          message.success('success change password');
        } else {
          message.error(`error: ${changePassword.system.message}`);
        }
      }
    },
    [changePassword],
  );

  return (
    <>
      <Avatar size={64} icon={<UserOutlined />} />
      <Typography.Title
        level={1}
      >
        {UserModule.getUsername()}
      </Typography.Title>
      <Space.Compact style={{ width: '100%' }}>
        <Input.Password
          placeholder="Reset Password"
          value={password}
          onChange={({ target: { value } }) => setPassword(value)}
        />
        <Button
          type="primary"
          onClick={
            () => {
              if (password) {
                changePasswordApi(password);
              }
            }
          }
          loading={fetching}
        >
          Submit
        </Button>
      </Space.Compact>
      <Divider />
      <Button
        type="primary"
        onClick={
          () => {
            UserModule.removeUserToken();
            navigate('/');
          }
        }
        danger
      >
        Logout
      </Button>
    </>
  );
}

export default Account;
