import axios from './axios';

const mtproto = {
  users: () => axios.put('mtproto/users'),
  userDetails: () => axios.put('mtproto/user_details'),
  login: (phone, password) => axios.put('mtproto/login', { phone, password }),
  codeCallback: (code) => axios.put('mtproto/code_callback', { code }),
};

export default mtproto;
