import React from 'react';
import { useNavigate } from 'react-router-dom';
import { UserOutlined } from '@ant-design/icons';
import { Switch, Menu, Avatar } from 'antd';
import PropTypes from 'prop-types';
import UserModule from '../modules/User';

function MainMenu({ localTheme, setLocalTheme }) {
  const navigate = useNavigate();
  return (
    <Menu
      mode="horizontal"
      items={[
        {
          key: 'switch-theme',
          disabled: true,
          label: (
            <Switch
              checked={localTheme}
              checkedChildren="🌙"
              unCheckedChildren="☀️"
              onChange={(value) => setLocalTheme(value)}
            />
          ),
        },
        ...UserModule.isAuthenticated() ? [{
          key: 'User',
          icon: <Avatar size={32} icon={<UserOutlined style={{ margin: 'auto' }} />} />,
          children: [
            {
              type: 'group',
              label: UserModule.getUsername(),
              children: [
                {
                  key: 'settings',
                  label: 'settings',
                  onClick: () => navigate('/user/account'),
                },
              ],
            },
          ],
        },
        {
          key: 'Mtproto',
          label: (
            <a
              onClick={(e) => {
                e.preventDefault();
                navigate('/mtproto');
              }}
              href="#mtproto"
            >
              Mtproto
            </a>
          ),
        }] : [],
      ]}
    />
  );
}

MainMenu.propTypes = {
  localTheme: PropTypes.bool,
  setLocalTheme: PropTypes.func,
};

MainMenu.defaultProps = {
  localTheme: true,
  setLocalTheme: () => { },
};

export default MainMenu;
